import React, { lazy, Suspense } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import lazyWithRetry from "utils/LazyWithRetry";

const Employees = ({ match }) => (
  <Suspense>
    <Switch>
      <Redirect exact from={`${match.url}`} to={`${match.url}/manage-employees`} />
      <Route
        path={`${match.url}/manage-employees`}
        component={lazyWithRetry(() => import(`./manage-employees`), "manage-employees")}
      />
      <Route
        path={`${match.url}/employee-status`}
        component={lazyWithRetry(() => import(`./employee-status`), "employee-status")}
      />
    </Switch>
  </Suspense>
);

export default Employees;
